import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './login'
import { configure } from '@testing-library/react';
import Core from './Core';
import Guide from './Guide';
import Api from './Api';
import Hook from './Hook';
const Routes = [
    
        
            {
                path:'/Home',
                exact:true,
                component:Home
            },
            {
                path:'/Doc',
                component:Doc,
                pri:true,
                routes : [
                    {
                        path:'/doc/core',
                        component:Core,
                    },
                    {
                        path:'/doc/guide',
                        component:Guide,                        
                    },
                    {
                        path:'/doc/api',
                        component:Api,
                    },
                    {
                        path:'/doc/hook',
                        component:Hook,
                    }
                    
                ]
            },
            {
                path:'/Tuto',
                component:Tuto,
                pri:true
            },
            {
                path:'/Blog',
                component:Blog
                
            },
            {
                path:'/Comm',
                component:Comm
            },
            {
                path:'/login',
                component:Login
            }

]
export default Routes;