const App = () => {
	return <Router >
		<Header />
		<Switch >
			{
				Routes.map(({...rest})=>{
					// if(pri){
					// 	return <PrivateRoute {...rest} />
					// }
					return <RouteWithSubRoutes {...rest} />
				})
			}
		</Switch>
		
	</Router>
}
export const  RouteWithSubRoutes = (route) => {
	// console.log(route);
	if(route.pri){
		console.log('1');
		console.log(route.routes)
		return(
			<PrivateRoute {...route} routes={route.routes}></PrivateRoute>
		)
	}
	else{
		
		return (
			<Route
			  path={route.path}
			  render={props => (
				// pass the sub-routes down to keep nesting
				<route.component {...props} routes={route.routes} />
			  )}
			/>
		  );
	}
	
  }
function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest}  />
					: (
						<Redirect
							to={ {
								pathname: "/login",
								state: location
							} }
						/>
					)
			}
		/>
	);
}


export default App;
